html {
	box-sizing: border-box;
	font-size: ($root-font-size / 16px) * 100%;

	@include mobile {
		font-size: ($root-font-size / 16px) * 100% * 0.75; // Уменьшить rem на 25% в маленьких дисплеях
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

img {
	max-width: 100%;
	height: auto;
}

.page {
	margin: 0;
	font-family: 'system-ui', sans-serif;
	font-style: normal;
	font-weight: 400;
	line-height: 1.5;
}

.link {
	display: inline-block;
	margin: 0;
	padding: 0;
	color: inherit;
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
	text-decoration: none;
}

.list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.visually-hidden {
	position: absolute;
	white-space: nowrap;
	width: 1px;
	height: 1px;
	overflow: hidden;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	margin: 0;
	padding: 0;
}

.is-outer {
	position: relative !important; // stylelint-disable-line declaration-no-important
	left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
	overflow: hidden;
}

.is-hidden {
	display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
	display: none;

	@include mobile {
		display: block;
	}
}

.error {
	border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
	border: none;
	background: transparent;
	cursor: pointer;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}
